:root{
    --primary-color:#004380;
    --primary-font-color:white;
    --secondary-font:"Julius Sans One" ; 
    --border:light-dark(rgb(118, 118, 118), rgb(133, 133, 133)) solid 1px;
    --border-radius:10px;
}



.break{
    background-color: rgb(0, 0, 0 , .1);
    height: 1px;
    width: -webkit-fill-available;
}


/*

Sign in and Sign up pages

*/

.SignContainer{
    display: grid;
    grid-template-columns: 50% 50%;
    height: 100svh;
}

.SignFormContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.SignWelcome{
    margin-bottom: 30px;
}
.SignFormContainer form {
    width: 60%;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    border: black solid 1px;
    padding: 60px 20px;
    border-radius: 20px;
}

.SignFormContainer input, h3 , h1 , h4 , .SignDifferent{
    width: -webkit-fill-available;
    height: 32px;
    border-radius: var(--border-radius);
    padding: 4px;
    border-color: var(--border) ;
}


.SignFormContainer .SignSubmit{
    background-color: var(--primary-color);
    color: var(--primary-font-color);
    font-size: large;
    padding: 10px;
    height: auto;
    cursor: pointer;

}
.SignDifferent{
    border: var(--border) ;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
    cursor: pointer;

}


.SignFormDiv{
    display: flex;
    justify-content: space-evenly;
    width: -webkit-fill-available;
    align-items: center;
}

.SignFormDiv p{
    margin: 0 4px;
}

.SignImageContainer{
    display: flex;
    justify-content: flex-end;
}

.SignImageContainer img{
    height: 100svh;
    width: 85%;
 
}

.SignFormFooter{
    display: flex;
    justify-content: center;
    width: -webkit-fill-available;
    align-items: center;
    gap: 10px;
}

@media screen and (max-width:1200px) {

    .SignImageContainer img{
        width: auto;
    }

    .SignDifferent p{
        font-size: .8rem;
    }
    
     
}

@media screen and (max-width:1010px) {
    .SignContainer{
        display: grid;
        grid-template-columns: auto;
        height: 100svh;
    }
    .SignImageContainer{
        display: none;
    }
}

@media screen and (max-width:500px) {
    .SignFormContainer form {
        width: 80%;
    }   
}


@media screen and (max-width:380px) {
    .SignFormContainer form {
        width: 85%;
    }   
}
/*

    Navigation bar 

*/

.NavBarContainer{
    background-color: var(--primary-color);
    color: var(--primary-font-color);
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px 20px;
/*     height: 40px;
 */}

.NavBarRight {
    display: flex;
    justify-content: flex-end;
    column-gap: 5px;
}
.NavBarLeft{
    display: flex;
    justify-content: flex-start;
}

.NavBarRight >p{
    padding-left:5px ;
    border-left: white solid 1px ;
}

.NavBarLanguage , .NavBarAccount{
    display: flex;
    align-items: center;
    column-gap: 5px;
    cursor: pointer;
}


/* 

Header 

*/

.HeaderContainer{
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px 20px;
    border-bottom: var(--border);
}

.HeaderLeft{
    display: flex;
    justify-content: flex-start;
    column-gap: 10px;
    
}


.HeaderLeft p{
    font-size: xx-large;
    font-family: var(--secondary-font) ,"Outfit" ,'Segoe UI', 'Roboto', 'Oxygen';
    font-weight: 700;
    text-decoration: none;
    color: var(--primary-color);
}

.HeaderRight{
    display: flex;
    justify-content: flex-end;
}

.HeaderSearch {
    display: flex;
    border: var(--border);
    border-radius: var(--border-radius);
    border-style: inset;

}

.HeaderSearch input, input:focus{
    border: none;
    font-size: large;
}

/* 

Footer

*/

.FooterContainer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 7px  20px;
    background-color: var(--primary-color);
    color: var(--primary-font-color);
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
    font-size: small;

}

.FooterLeft{
    display: flex;
    column-gap: 15px;
}

@media screen and (max-width:500px) {
    .FooterContainer{
        font-size: xx-small;
    }
}


/* 

OffersContainer

*/

.OffersContainer{
    background-color: black;
    color: var(--primary-font-color);
    font-weight: 100;
    text-align: center;
    padding:5px 0;
}




@media screen and (max-width:550px) {
    .OffersContainer{
        font-size: small;
    }
}

