.LandingContainer img{
    position: absolute;
    width: 100%;
}

 .LandingTextWrapper{
    position: absolute;
    display: grid;
    width: -webkit-fill-available;
   
}

.LandingContainer p{
    font-family:"Stick No Bills", "sans-serif";
    display: inline-block;
    width: -moz-fit-content;
    width: fit-content;
}

 .LandingImageName{
    font-weight: 900;
    font-size: 3rem;
    margin-left: 10%;
}

.BrandVoice{
    font-size: 2rem;
    font-weight: 700;
    border: black solid 1px;
    margin-right: 5%;
    padding: 5px;
    justify-self: flex-end;
    margin-left: 10%;
}

@media screen and (max-width:900px) {
.BrandVoice{font-size: 1.5rem;}

}

@media screen and (max-width:800px) {
    .LandingImageName{font-size: 2rem;}
    
}

@media screen and (max-width:600px) {
        .BrandVoice{font-size: 1rem;}
        
}
@media screen and (max-width:500px) {
    .LandingImageName{font-size: 1.5rem;}
}

@media screen and (max-width:440px) {
    .BrandVoice{font-size: .8rem;}
    
}

@media screen and (max-width:400px) {
    .LandingImageName{font-size: 1rem;}
}